<template>
  <SetHeader
    :name="currentName"
    :setName="set?.name"
    :saveFishka="saveFishka"
    :disabled="isSaved"
  ></SetHeader>
  <Test v-if="isTest"></Test>
  <Terms
    v-if="isTerms"
    :inputTerms="set.terms"
    :updateIndex="updateIndex"
    :addTerm="addTerm"
    @saveFishkaTerms="saveFishkaTerms"
  ></Terms>
  <Settings
    v-if="isSettings"
    :set="set"
    @saveFishka="saveFishkaMetadata"
    :deleteFishka="deleteFishka"
    :deleteFishkaProgress="deleteFishkaProgress"
  >
  </Settings>
  <SetFooter :currentPath="currentPath" :setPath="setCurrentPath"></SetFooter>
</template>

<script>
  import { saveFishkaToDb, getFishka, deleteFishka } from "@/data";
  import Settings from "./Settings.vue";
  import Terms from "./Terms.vue";
  import Test from "./Test.vue";
  import SetFooter from "/src/components/SetFooter.vue"
  import SetHeader from "/src/components/SetHeader.vue"
import { toRaw } from "vue";
  export default {
    name: 'SetView',
    components: {
      Settings,
      Terms,
      Test,
      SetFooter,
      SetHeader,
    },
    mounted() {
      this.id = this.$route.params.set_id
      if (this.id === "new") {
        this.setCurrentPath("settings")
      } else {
        getFishka(+this.id).then(v => {
          this.set = v
        })
      }
    },
    methods: {
      setCurrentPath(path) {
        this.currentPath = path
        switch (path) {
          case "test":
            this.currentName = "Test"
            this.isTest = true
            this.isTerms = false
            this.isSettings = false
            break
          case "terms":
            this.currentName = "Terms"
            this.isTest = false
            this.isTerms = true
            this.isSettings = false
            break
          case "settings":
            this.currentName = "Settings"
            this.isTest = false
            this.isTerms = false
            this.isSettings = true
            break
        }
      },
      saveFishkaMetadata(v) {
        this.isSaved = false
        this.metadata = v
        console.log(this.isSaved)
      },
      saveFishkaTerms(t) {
        this.isSaved = false
        this.terms = t
      },
      deleteFishkaProgress() {
        this.isSaved = true
        const set = structuredClone(toRaw(this.set))
        if (this.metadata !== undefined) {
          set.name = this.metadata.name
          set.category = this.metadata.category
        }
        set.terms.forEach(v => {
          v.familiarity = {cards: 0, choice: 0, quiz: 0, learn: 0}
        })
        saveFishkaToDb(set).then(v => { this.set = v })
      },
      deleteFishka() {
        deleteFishka(this.set.id).then(() => { this.$router.push("/fishki")})
      },
      saveFishka() {
        this.isSaved = true
        const set = structuredClone(this.set)
        if (this.metadata !== undefined) {
          set.name = this.metadata.name
          set.category = this.metadata.category
        }
        if (this.terms !== undefined) {
          set.terms = toRaw(this.terms)
        }
        saveFishkaToDb(set).then(v => { this.set = v })
      },
    },
    data() {
      return {
        isSaved: true,
        currentPath: "test",
        currentName: "Test",
        isSettings: false,
        isTerms: false,
        isTest: true,
        terms: undefined,
        metadata: undefined,
        set: {
          name: "",
          category: "",
          terms: []
        }
      }
    }
  }
</script>

